body {
  background: #666;
}

.container {
  padding-top: 5rem;
  width: 800px;
  margin: 0 auto;
}

.card {
  border-radius: 6px;
  background: #fff;
  padding: 1rem;
  box-shadow: 2px 3px 10px rgba(0,0,0,0.2);
}

.btn {
  color: #42b983;
  position: relative;
  border-radius: 99px;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  font-weight: 700;
  outline: none;
  background: #fff;
  transition: all 0.22s;
  cursor: pointer;
  margin-right: 1rem;
}

.btn.primary {
  background: #42b983;
  color: #fff;
}

.btn.active {
  box-shadow: inset 1px 1px 1px rgba(0,0,0,0.3);
}

.btn:hover {
  cursor: pointer;
  opacity: 0.8;
}

input[type=file] {
  display: none;
}

.preview {
  display: flex;
  flex-wrap: wrap;
  padding: .5rem;
}

.preview-image {
  margin-bottom: .5rem;
  margin-right: .5rem;
}

.preview-image img {
  width: 180px;
  height: auto;
}